import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatButton } from '@angular/material/button';
import { MatTableDataSource } from '@angular/material/table'
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { IPagoTipo } from '../models/pago-tipo';
import { MediosDeCobroService } from '../service/medios-de-cobro.service';
import { Subscription, debounceTime, distinctUntilChanged } from 'rxjs';
import { SaveComponent } from './save/save.component';
import { IFilter } from '@shared/models/filter';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatDialog } from '@angular/material/dialog';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
@Component({
    selector: 'app-medios-de-cobro',
    templateUrl: './medios-de-cobro.component.html',
    styleUrls: ['./medios-de-cobro.component.scss']
})
export class MediosDeCobroComponent implements OnInit, OnDestroy {
    public dataSource: MatTableDataSource<IPagoTipo> = new MatTableDataSource();
    headers = ['Descripción', 'Descuento / Recargo', 'Lleva Número', 'Lleva Vencimiento', 'Fecha de emision', 'Fecha de cobro', 'Duplica número', 'Lleva banco', 'Activo', 'Fondo'];
    column_params = ['descripcion', 'porcentaje', 'lleva_numero', 'lleva_vencimiento', 'lleva_fecha_emision', 'lleva_fecha_cobro', 'duplica_numero', 'lleva_banco', 'activo', 'fondo', 'acciones']
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['descripcion'];
    filter: IFilter = {
        lleva_numero: -1,
        lleva_vencimiento: -1,
        lleva_fecha_emision: -1,
        lleva_fecha_cobro: -1,
        duplica_numero: -1,
        lleva_banco: -1,
        activo: -1,
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.administracion.view_ven_pago_tipos + '?';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;

    constructor(
        private _mediosDeCobroService: MediosDeCobroService,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
    ) {
        /**
        * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
        */
        this.funcionesObjeto = [
            {
                icono: 'edit',
                nombre_boton: "Editar",
                functionName: 'editar',
                //functionParams: 'id' // vacio o parametros, se manda desde el hijo el $event.data.[param]
            },
            {
                icono: 'delete',
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
            },
        ];
    }
    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;

    ngOnInit(): void {
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(250),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    /**
    * Esta funcion se encarga de levantar los datos que se van a filtrar en el componente general del filtro
    * @param origin 
    */
        onFilterMenu(origin: MatButton) {
            this._menuService
                .open(FilterMenuComponent, origin._elementRef, {
                    filter: this.filter,
                })
                .afterClosed()
                .subscribe({
                    next: filter => {
                        if (filter) {
                            this.filter = filter;
                            this.extraParams += this.filter?.activo != -1 ? "&filter[activo]=" + this.filter.activo : '';
                            this.extraParams += this.filter?.lleva_banco != -1 ? "&filter[lleva_banco]=" + this.filter.lleva_banco : '';
                            this.extraParams += this.filter?.lleva_numero != -1 ? "&filter[lleva_numero]=" + this.filter.lleva_numero : '';
                            this.extraParams += this.filter?.lleva_vencimiento != -1 ? "&filter[lleva_vencimiento]=" + this.filter.lleva_vencimiento : '';
                            this.extraParams += this.filter?.lleva_fecha_cobro != -1 ? "&filter[lleva_fecha_cobro]=" + this.filter.lleva_fecha_cobro : '';
                            this.extraParams += this.filter?.lleva_fecha_emision != -1 ? "&filter[lleva_fecha_emision]=" + this.filter.lleva_fecha_emision : '';
                            this.extraParams += this.filter?.duplica_numero != -1 ? "&filter[duplica_numero]=" + this.filter.duplica_numero : '';
                            this.tabla.filters(this.filtroBusqueda);
                            // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());
                        }
                    },
                });
        }

            /**
    * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
    * @param event 
    */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateMedioDeCobro(event);
                break;
            case 'eliminar':
                this.deleteMedioDeCobro(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveMedioDeCobro() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateMedioDeCobro(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteMedioDeCobro(event) {
        this.confirm.open({
            title: "Eliminar Medio de cobro " + event.params.data.descripcion,
            message: "¿Desea eliminar la Medio de cobro: " + event.params.data.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._mediosDeCobroService.deleteMedioDeCobro(event.params.data.idpagotipo).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Medio de cobro eliminado con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error(error);
                            alert("Este Medio de cobro no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }
    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}