import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/menu.service';

@Component({
  selector: 'app-filter-menu-medios-de-cobro',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  public proveedorespp: any[];
  constructor(@Inject('MENU_DATA') public data: any, private menuService: JedsMenuService) { }

  ngOnInit(): void {
    /**
    * Formulario de filtros
    */
    this.formFilters = new FormGroup({
      lleva_numero: new FormControl(('')),
      lleva_vencimiento: new FormControl(('')),
      lleva_fecha_emision: new FormControl(('')),
      lleva_fecha_cobro: new FormControl(('')),
      duplica_numero: new FormControl(('')),
      lleva_banco: new FormControl(('')),
      activo: new FormControl(('')),
    });
    this.formFilters.patchValue(this.data.filter);
  }

  resetFilters() {
    this.formFilters.reset({
      lleva_numero: -1,
      lleva_vencimiento: -1,
      lleva_fecha_emision: -1,
      lleva_fecha_cobro: -1,
      duplica_numero: -1,
      lleva_banco: -1,
      activo: -1,
    });
  }

  onCloseMenu(data: any = null) {
    this.menuService.close(data);
  }
}